  --main-scrollbar-width: 6px;
  --main-scrollbar-color: var(--primary-4);
  --main-scrollbar-thumb-color: var(--purple-4);
  --text-xxl: clamp(36px, 7.5vw, 80px);
  --text-xl: clamp(28px, 5vw, 44px);
  --text-lg: clamp(24px, 4vw, 36px);
  --text-md: clamp(20px, 3.5vw, 32px);
  --text-sm: clamp(16px, 3vw, 28px);
  --text-xs: 14px;
  --text-xxs: 12px;
  --main-t-rules: 0.25s 0.05s ease;
